:root {
  --color--background: #fff;
  --color--text: #000;
  --color--accent: #6a00ff;
}

html, body {
  margin: 0;
}

html {
  color: #000;
  font-family: sans-serif;
  line-height: 1.3;
}

header, main, footer {
  padding: 2rem;
}

h1 {
  font-size: 4rem;
  font-weight: 400;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

p {
  max-width: 54rem;
  margin: 1rem 0;
}

a {
  color: inherit;
}

button, input[type="button"], input[type="submit"], .button {
  font: inherit;
  color: var(--color--background);
  background-color: #000;
  background-color: var(--color--text);
  box-shadow: none;
  cursor: pointer;
  border: none;
  border-radius: 0;
  outline: none;
  padding: .1em .6em;
}

button:not(.does-not-exist):hover, input[type="button"]:hover, input[type="submit"]:hover, .button:not(does-not-exist):hover {
  color: var(--color--text);
  background-color: #6a00ff;
  background-color: var(--color--accent);
}

code {
  background-color: #eee;
  border-radius: .5rem;
  max-width: 54rem;
  margin: 1rem 0;
  padding: .5rem;
  display: block;
}

code pre {
  margin: 0;
}

.Items {
  grid-gap: 2vw;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: min-content;
  align-content: flex-start;
  align-items: flex-start;
  gap: 2vw;
  width: 100%;
  display: grid;
}

.Item {
  aspect-ratio: 1;
  background-image: linear-gradient(#fff 0%, #666 100%);
  border-radius: .5rem;
  margin: 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 .2rem 1rem #0006;
}

.Item-ui {
  z-index: 20;
  margin: .5rem;
  position: absolute;
  bottom: 0;
  right: 0;
}

#three-perf-ui {
  position: absolute !important;
}
